      <template #over-table="{item}">
        <div class="row justify-content-between mx-0 my-0 px-0 py-1">
          <div
            class="col-10 col-xl-5 col-lg-5 col-md-7 col-sm-10 col-xs-10 mx-0 my-0 px-0 py-0 row align-items-center"
          >
            <breadcrumb />
          </div>
          <div
            class="col-2 col-xl-7 col-lg-7 col-md-5 col-sm-2 col-xs-2 d-flex justify-content-end px-0"
            style="transform: scale(0.9) !important; transform-origin: center right !important;"
          >
            <CButtonGroup class="hide-md hide-sm hide-xs">
              <CButton disabled color="light" size="sm">
                <b-icon icon="table" variant="primary"></b-icon>
                {{ table.label }}
              </CButton>

              <CButton disabled color="light" size="sm">
                <b-icon icon="info-square" variant="primary"></b-icon>
                ({{ items.length }})条数据
              </CButton>
            </CButtonGroup>

            <CButtonGroup class="" size="sm">
              <i class="hide-sm hide-xs">
                <CButton color="light" size="sm" @click="check_all_item()">
                  <b-icon
                    icon="check-square"
                    :variant="all_checked ? 'secondary' : 'primary'"
                  />
                  {{ all_checked ? "取消全选" : "全选" }}
                </CButton>
                <CButton
                  color="light"
                  size="sm"
                  @click="get_confirm(delete_item, item)"
                >
                  <b-icon icon="trash2" variant="danger"></b-icon>
                  删除选择项
                </CButton>
              </i>
              <CButton color="success" size="sm">
                <router-link
                  :to="'/' + route.app + '/' + route.table + '/add'"
                  class="text-white"
                >
                  <b-icon icon="plus-circle" color="light" variant=""></b-icon>
                  <span class="ml-1 hide-sm hide-xs">新增</span>
                </router-link>
              </CButton>

              <b-button
                v-if="false"
                href="#"
                variant="primary"
                size="sm"
                @click="$bvModal.show('modal_update_file')"
              >
                <b-icon icon="file-arrow-up" variant=""></b-icon>
                导入xlsx文件
              </b-button>
            </CButtonGroup>

            <b-button-group class="ml-2" v-if="false">
              <b-button
                variant="warning"
                @click="show_toast(route.table, '条数据', 'success', true)"
                class="btn-sm"
              >
                当前数据表状态
              </b-button>
            </b-button-group>
          </div>
        </div>
      </template>

      <template #checkbox-filter="">
        <i style="visibility: hidden;"></i>
      </template>
      <template #control-filter="">
        <i style="visibility: hidden;"></i>
      </template>
      <template #index-filter="">
        <i style="visibility: hidden;"></i>
      </template>
      <template #checkbox="{item}" class="">
        <td class="" :style="style.td">
          <b-form-checkbox name="checkbox" :value="item.pk" />
        </td>
      </template>
      <template #control="{item}">
        <td class="text-left">
          <router-link
            :to="'/' + route.app + '/' + route.table + '/edit/' + item.pk"
          >
            <b-icon icon="pencil-square" variant="primary" class="mr-2" />
          </router-link>
          <a @click="get_confirm(delete_item, item)">
            <b-icon icon="trash2" variant="danger"></b-icon>
          </a>
        </td>
      </template>
      <template #index="{index}">
        <td class="">
          {{ index + 1 }}
        </td>
      </template>
      <template #status="{item}">
        <td class="text-right">
          <!-- <b-icon-arrow-up class="text-success" v-if="(item.status == 'Active')"></b-icon-arrow-up> -->
          <b-icon
            icon="check-circle"
            variant="success"
            v-if="item.status == 'Active'"
          ></b-icon>
        </td>
      </template>
      <template #url="{item}">
        <td class="text-left">
          <router-link :to="'/table/' + item.table">
            <b-icon icon="box-arrow-up-right" variant="primary"></b-icon>
          </router-link>
        </td>
      </template>

      <template #show_details="{item, index}">
        <td class="">
          <a href="#" @click="toggleDetails(item, index)">
            <b-icon v-if="Boolean(item._toggled)" icon="chevron-down"></b-icon>
            <b-icon v-else icon="chevron-right"></b-icon>
            <!-- <i v-if="Boolean(item._toggled)" class="fa fa-angle-down"></i> -->
            <!-- <i v-else class="fa fa-angle-right"></i> -->
            <!-- {{Boolean(item._toggled) ? 'Hide' : 'Show'}} -->
          </a>
        </td>
      </template>
      <template #details="{item}">
        <CCollapse :show="Boolean(item._toggled)" :duration="collapseDuration">
          <CCardBody>
            <CMedia :aside-image-props="{ height: 102 }">
              <h4>
                {{ item.username }}
              </h4>
              <p class="text-muted">User since: {{ item.registered }}</p>
              <CButton size="sm" color="info" class="">
                User Settings
              </CButton>
              <CButton size="sm" color="danger" class="ml-1">
                Delete
              </CButton>
            </CMedia>
          </CCardBody>
        </CCollapse>
      </template>
    </CDataTable>

    <b-modal id="modal_update_file" size="lg">
      <template v-slot:modal-header="{ close }" v-if="false">
        <!-- Emulate built in modal header close button action -->
        <b-button size="sm" variant="outline-danger" @click="close()">
