        </b-button>
        <h5>Modal Header</h5>
      </template>

      <template v-slot:default="{}">
        <b-list-group>
          <b-list-group-item
            class="d-flex justify-content-center align-items-center"
          >
            <b-icon
              icon="info-circle-fill"
              scale="2"
              variant="info"
              class="mr-3"
            />
            请确保数据在xlsx文件中名为
            <b-button disabled variant="outline-primary" size="sm" class="mx-2">
              {{ sheet_name || label }}
            </b-button>
            的sheet表中
          </b-list-group-item>
