          :state="Boolean(file)"
          size="sm"
          placeholder="选择xlsx文件 或 拖拽到此处..."
          drop-placeholder="Drop file here..."
          accept=".xlsx"
          @change="set_progress(0)"
        ></b-form-file>
      </template>

      <template v-slot:modal-footer="{ ok, cancel }">
        <!-- Emulate built in modal footer ok and cancel button actions -->
        <b-button size="sm" variant="success" @click="update_file()">
          <b-icon icon="file-arrow-up" variant=""></b-icon>
          开始导入
        </b-button>
        <b-button size="sm" variant="danger" @click="cancel()">
          取消
        </b-button>
        <!-- Button with custom close trigger value -->
        <!-- <b-button size="sm" variant="outline-secondary" @click="hide('forget')"> -->
        <!-- Forget it -->
        <!-- </b-button> -->
      </template>
    </b-modal>

    <b-modal
      id="modal_response"
      title="xlsx文件导入结果"
      size="xl"
      static
      no-close-on-backdrop
      @hide="set_progress(0)"
    >
      <template v-slot:default="{}">
        <b-button-group vertical v-if="response.data.error">
          <b-button class="btn-sm btn-light">
            <b-icon
              icon="check"
              class="border border-info rounded"
              variant="info"
            />
            导入成功行数 ({{ response.data.success }})
          </b-button>
          <b-button class="btn-sm btn-light">
            <b-icon
              icon="x"
              class="border border-danger rounded"
              variant="danger"
            />
            导入失败行数 ({{ response.data.error }})
          </b-button>
        </b-button-group>
        <hr v-if="response.data.error" />
        <div v-html="response.msg"></div>
      </template>

      <template v-slot:modal-footer="{}">
        <!-- Emulate built in modal footer ok and cancel button actions -->
        <b-button
          :disabled="response.code < -1 ? true : false"
          size="sm"
          :variant="response.code < -1 ? 'outline-secondary' : 'success'"
          @click="download_file()"
        >
          <b-icon icon="file-earmark-arrow-down" variant="" />
          下载已批注xlsx文件副本
        </b-button>
      </template>
    </b-modal>
    <!-- </CCardBody> -->
  </div>
</template>
<script>
const unshift_fields = [
  {
    key: "checkbox",
    label: "选择",
    _classes: "text-nowrap text-truncate"
  },
  {
    key: "control",
    label: "操作",
    _classes: "text-nowrap text-truncate"
  }
  // {
  //   key: "index",
  //   label: "序号",
  //   _classes: "text-nowrap text-truncate"
  // }
];

export default {
  name: "Table",
  props: {},
  data() {
    return {
      table: {
        app: {
          name: "",
          label: ""
        }
      },
      api: {
        lens: ""
      },
      sheet_name: "",
      items: [],
      fields: [],
      details: [],
      collapseDuration: 0,
      checkbox_array: [],
      all_checked: false,
      file: null,
      progress: 0,
      response: {
        code: null,
        msg: null,
        data: {
          success: null,
          error: null
        }
      },
      style: {
        td: {
          padding: "0.18rem !important",
          transform: "scale(0.8) !important"
        }
      }
    };
  },

  computed: {
    route() {
      return this.$store.getters.route;
    }
  },

  methods: {
    getBadge(status) {
      switch (status) {
        case "Active":
          return "success";
        case "Inactive":
          return "secondary";
        case "Pending":
          return "warning";
        case "Banned":
          return "danger";
        default:
          "primary";
      }
    },

    toggleDetails(item) {
      this.$set(this.items[item.id], "_toggled", !item._toggled);
      this.collapseDuration = 300;
      this.$nextTick(() => {
        this.collapseDuration = 0;
      });
    },

    set_table_height() {
      // 设置table与页面相适应的高度
      let doc_height = document.documentElement.clientHeight - 152;
      // let table = document.getElementById("table");
      // table.style.maxHeight = doc_height + "px";

      let tables = document
        .getElementById("table")
        .getElementsByClassName("table-responsive");
      for (var i = tables.length - 1; i >= 0; i--) {
        // 当前这个元素在table的control栏与分页栏中间
        tables[i].style.maxHeight = doc_height + "px";
        tables[i].style.marginBottom = 0.2 + "rem";
      }
    },

    get_data() {
      // 获取当前table的所有值
      this.fields = [];
      this.items = [];
      this.$axios.get(this.api.lens).then(response => {
        if (Number(response.data.code) < 0) {
          this.show_toast("加载状态", "数据请求错误", "danger", true);
        } else {
          this.table = response.data.data;
          // console.log("get_data response.data.data", this.table);
          // this.set_sheet_name();

          let fields_data = Object.keys(this.table.fields).map(key => {
            // 增加pk 适应前端渲染
            if (key == "id") {
              this.table.fields["pk"] = this.table.fields[key];
              key = "pk";
            }

            let field = {
              key,
              label: this.table.fields[key],
              _style: "white-space: pre-wrap;",
              // _style: "min-width: 180px!important;max-width: 260px!important;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;",
              _classes: "text-nowrap text-truncate"
            };
            return { ...field, key };
          });
          // console.log('fields_data',fields_data)
          let fields_tmp = [];
          Array.prototype.push.apply(fields_tmp, unshift_fields);
          Array.prototype.push.apply(fields_tmp, fields_data);
          this.fields = fields_tmp;
          // console.log('unshift_fields',unshift_fields)
          // console.log("this.fields", this.fields.length, this.fields);

          this.items = this.table.items.map((item, id) => {
            // item["_style"] = "min-width: 180px!important;max-width: 260px!important;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;";
            // item["_classes"] = "text-truncate";
            // console.log('item', item)
            item["pk"] = item["id"];
            return { ...item, id };
          });
          // console.log("this.fields", this.fields);
          // console.log("this.items", this.items);
          this.show_toast("加载状态", "数据渲染完成", "success", true);
        }
      });
    },

    delete_item(item) {
      // 删除选择的checkbox
      // console.log("delete_item", item);
      let data = {
        action: "multi_delete",
        // pk: ['223','4','6'],
        pk_list: item ? [item.pk] : this.get_checked_pk()
      };
      console.log("delete_item data", data);

      // let params = this.$qs.stringify(data, { allowDots: true })
      // let params = this.trans_urlencode(data);
      // console.log("params", typeof params, params);

      this.$axios
        .delete(this.api.lens, {
          data,
          timeout: 3000
        })
        .then(response => {
          console.log("multi_delete rps", response);
          let content = response.data.data.msg
            ? String(response.data.data.msg)
            : response.data.msg;
          let variant = response.data.code == 1 ? "success" : "danger";
          this.show_toast(response.data.msg, content, variant, true);
          if (response.data.code == 1) {
            this.get_data();
          }
        });
    },

    check_all_item() {
      // 全选checkbox
      Array.from(this.checkbox_array).map(item => {
        item.checked = !this.all_checked;
      });
      this.all_checked = !this.all_checked;
      // console.log("this.checkbox_array", this.checkbox_array[1].value);
    },

    get_checked_pk() {
      // 获取所有checkbox的pk
      this.checked_pk = [];
      Array.from(this.checkbox_array).map(item => {
        // console.log("checkbox_array checked",item.checked)
        if (item.checked) {
          this.checked_pk.push(item.value);
        }
      });
      return this.checked_pk;
    },

    get_confirm(callback, item) {
      // 弹出确认框
      // console.log("callback", typeof callback, typeof item, item);
      this.confirm = "";
      this.$bvModal
        .msgBoxConfirm("确认?", {
          // title: "Please Confirm",
          size: "sm",
          buttonSize: "sm",
          okVariant: "danger",
          okTitle: "YES",
          cancelTitle: "NO",
          footerClass: "p-2",
          hideHeaderClose: false,
          centered: true
        })
        .then(value => {
          this.confirm = value;
          console.log("this.confim", this.confirm);
          if (value) {
            callback(item);
          }
        })
        .catch(err => {
          // An error occurred
          console.log("err", err);
        });
    },

    show_toast(title, content, variant = "primary", append = false) {
      // 消息框
      this.$bvToast.toast(content, {
        title,
        toaster: "b-toaster-bottom-full",
        variant,
        solid: true,
        appendToast: append,
        autoHideDelay: 3000
      });
    },

    show_modal() {
      // 确认框
      this.boxOne = "";
      this.$bvModal
        .msgBoxOk("Action completed")
        .then(value => {
          this.boxOne = value;
        })
        .catch(err => {
          // An error occurred
          console.log("err", err);
        });
    }
  },

  beforeCreate() {},

  beforeMount() {
    this.$store.commit("route", this.$route.params);
    // console.log("this.route", this.route);
    this.api.lens = "/api/v1/" + this.route.app + "/" + this.route.table;
    this.$utils.get_index(this, "/api/v1/app");
  },

  mounted() {
    this.set_table_height();
    this.get_data();
  },

  updated() {
    // 挂载所有的checkbox
    this.checkbox_array = document
      .getElementById("table")
      .querySelectorAll('[type="checkbox"]');
    // console.log("updated");
  }
};
</script>
