/* 本配置文件功能：
1. 区分加工开发环境与生产环境的不同配置例如根服务器地址。
2. 并不能解决生产环境跨域问题。
生产环境跨域需在生产环境用其他方案例如Nginx解决。
*/

let HOST = {};
let publicPath;
let assetsDir;
//开发环境下的代理地址，解决本地跨域跨域，配置在vue.config.js里
let PROXY = {};

// 前后端使用同一个地址的前后端分离 适合在这里进行对配置进行加工
if (process.env.VUE_APP_HOST && process.env.VUE_APP_PORT) {
  console.log("if process.env.VUE_APP_HOST");
  PROXY.HTTP =
    "http://" + process.env.VUE_APP_HOST + ":" + process.env.VUE_APP_PORT;
} else {
  console.log("else process.env.VUE_APP_HOST");
  PROXY.HTTP = "";
}

// 前后端使用不同地址的前后端分离 适合在这里进行对配置进行加工
if (process.env.NODE_ENV === "development") {
  // 开发环境下的配置
  publicPath = "./";
} else {
  // 生产环境下的配置
  publicPath = "o/ecc_emergency";
}

HOST.HTTP = "";
// HOST.WS = "";
assetsDir = "static/emergency";

// export default { HTTP, PROXY, publicPath, assetsDir };
exports.HOST = HOST;
exports.PROXY = PROXY;
exports.publicPath = publicPath;
exports.assetsDir = assetsDir;
