<template>
  <!-- <b-breadcrumb class="my-0 py-1" :items="breadcrumb" /> -->
  <ol :class="class_ol">
    <li :class="class_li">
      <router-link :to="'/'" :class="class_a">
        <!-- <b-icon icon="house-fill" /> -->
        <img
          :src="'static/emergency/imgs/icons/' + 'ecc' + '.svg'"
          width="20"
          height="20"
          class="mr-1"
        />
      </router-link>
    </li>
    <li :class="class_li" v-if="breadcrumb.app">
      <a
        :class="class_a"
        style="cursor: pointer;"
        target="_blank"
        @click="$utils.route_push($router, '/' + breadcrumb.app.name)"
      >
        <img
          :src="'static/emergency/imgs/icons/' + breadcrumb.app.name + '.svg'"
          width="16"
          height="16"
          class="mr-1"
        />
        <div>{{ breadcrumb.app.label }}</div>
      </a>
    </li>
    <li :class="class_li" v-if="breadcrumb.table">
      <a
        :class="class_a"
        style="cursor: pointer;"
        @click="
          $utils.route_push(
            $router,
            '/' + breadcrumb.app.name + '/' + breadcrumb.table.name
          )
        "
      >
        {{ breadcrumb.table.label }}
      </a>
    </li>
    <li :class="class_li" v-if="breadcrumb.action">
      <span class="d-flex align-items-center text-muted">
        {{ breadcrumb.action.text }}
      </span>
    </li>
    <li :class="class_li" v-if="breadcrumb.pk">
      <span class="d-flex align-items-center text-muted">
        {{ breadcrumb.pk.text }}
      </span>
    </li>
  </ol>
</template>
<script>
export default {
  name: "breadcrumb",

  props: {
    dark: {
      type: Boolean,
      default() {
        return false;
      }
    }
  },

  data() {
    return {};
  },

  watch: {},

  computed: {
    index() {
      return this.$store.getters.index;
    },

    route() {
      return this.$store.getters.route;
    },

    route_name() {
      // console.log("this.$route.name", this.$route.name)
      return this.$route.name;
    },

    breadcrumb() {
      // 根据index、route构造breadcrumb
      let breadcrumb = {};
      let app = this.route["app"];
      if (app && this.index[app]) {
        breadcrumb["app"] = this.index[app];
        breadcrumb["app"]["to"] = "/" + app;
        breadcrumb["app"]["active"] = false;
        // console.log("breadcrumb app", app, breadcrumb);

        let table = this.route["table"];
        // console.log("breadcrumb table 0", table, breadcrumb);
        if (table && this.index[app]["tables"]) {
          // console.log("breadcrumb table 1", breadcrumb, this.route_name, );
          breadcrumb["table"] = this.index[app]["tables"][table];
          breadcrumb["table"]["to"] = "/" + app + "/" + table;
          breadcrumb["table"]["active"] = true;

          if (["Add", "Edit"].includes(this.route_name)) {
            // console.log("breadcrumb action", this.route, pk);
            breadcrumb["table"]["active"] = false;
            breadcrumb["action"] = {
              text: this.route_name
            };
          }

          let pk = this.route["pk"];
          // console.log("breadcrumb pk", this.route, pk);
          if (pk) {
            breadcrumb["pk"] = {
              text: pk
            };
          }
        }
      }
      return breadcrumb;
    },

    class_ol() {
      let klass = "breadcrumb my-0 py-1";
      if (this.dark) {
        klass += " bg-dark";
      }
      return klass;
    },
    class_li(item) {
      let klass = "breadcrumb-item";
      if (item.active) {
        klass += " active";
      }
      return klass;
    },
    class_a() {
      let klass = "d-flex align-items-center";
      if (this.dark) {
        klass += " text-white";
      }
      return klass;
    }
  },

  methods: {},

  beforeCreate() {},

  beforeMount() {},

  mounted() {},

  updated() {}
};
</script>
<style scoped lang="scss">
ol.breadcrumb {
  font-size: 0.8rem !important;
}
</style>
