      id="table"
      class="table table-sm scrolltable"
      style="font-size: 0.8rem !important;"
      :items="
        index[route['app']] ? Object.values(index[route['app']].tables) : []
      "
      :fields="fields"
      :items-per-page="100"
      hover
      sorter
      pagination
      small
      fixed
      striped
      outlined
      responsive
    >
      <template #over-table="">
        <div class="row justify-content-between mx-0 my-0 px-0 py-1">
          <div
            class="col-10 col-xl-5 col-lg-5 col-md-7 col-sm-10 col-xs-10 mx-0 my-0 px-0 py-0 row align-items-center"
