            class="rounded-circle bg-success"
          />
        </td>
      </template>
      <template #app="{}">
        <td class="text-left">
          {{ index[route["app"]].label }} ({{ index[route["app"]].name }})
        </td>
      </template>
      <template #url="{item}" v-if="false">
        <td class="text-left">
          <router-link :to="'/' + item.table">
            <b-icon icon="box-arrow-up-right" variant="primary" />
          </router-link>
        </td>
      </template>
      <template #label="{item}">
        <td class="text-left">
          <router-link :to="'/' + index[route['app']].name + '/' + item.name">
            {{ item.label }}
          </router-link>
        </td>
      </template>
      <template #manage="{item}">
        <td class="text-left">
          <a class="mr-2" target="_blank" @click="router_push(item.name)">
            <b-icon icon="info-square" variant="success" />
          </a>
          <a
            class="mr-2"
            target="_blank"
            @click="router_push(item.name, 'add')"
          >
            <b-icon icon="plus-circle-fill" variant="success" />
          </a>
        </td>
      </template>
      <template #show_details="{item, index}">
        <td class="">
          <a href="#" @click="toggleDetails(item, index)">
            <b-icon v-if="Boolean(item._toggled)" icon="chevron-down"></b-icon>
            <b-icon v-else icon="chevron-right"></b-icon>
            <!-- <i v-if="Boolean(item._toggled)" class="fa fa-angle-down"></i> -->
            <!-- <i v-else class="fa fa-angle-right"></i> -->
            <!-- {{Boolean(item._toggled) ? 'Hide' : 'Show'}} -->
          </a>
        </td>
      </template>
      <template #details="{item}">
        <CCollapse :show="Boolean(item._toggled)" :duration="collapseDuration">
          <CCardBody>
            <CMedia :aside-image-props="{ height: 102 }">
              <h4>
                {{ item.username }}
              </h4>
              <p class="text-muted">User since: {{ item.registered }}</p>
              <CButton size="sm" color="info" class="">
                User Settings
              </CButton>
              <CButton size="sm" color="danger" class="ml-1">
                Delete
              </CButton>
            </CMedia>
          </CCardBody>
        </CCollapse>
      </template>
    </CDataTable>
