</template>
<script>
export default {
  props: {
    index: {
      type: Object
      // default() {
      //   return this.$store.getters.index;
      // }
    },
    route: {
      type: Object
      // default() {
      //   return this.$store.getters.route;
      // }
    },
    app: {
      type: Object
      // default() {
      //   return this.index[this.route["app"]];
      // }
    }
  },

  data() {
    return {
      fields: [
        // {
        //   key: "index",
        //   label: "序号",
        //   _style: "width:5%;",
        //   _classes: "text-right"
        // },
        // {
        //   key: "status",
        //   label: "状态",
        //   _style: "width:6%;",
        //   _classes: "text-right"
        // },
        // {
        //   key: "app",
        //   label: "所属App",
        //   _style: "",
        //   _classes: "text-left"
        // },
        // {
        //   key: "table",
        //   label: "数据表",
        //   _style: "",
        //   _classes: "text-left"
        // },
        {
          key: "label",
          label: "数据表名",
          _style: "",
          _classes: "text-left"
        },
        // { key: "url", label: "链接", _style: "", _classes: "text-left" },
        { key: "manage", label: "管理", _style: "", _classes: "text-left" }
        // {
        //   key: "show_details",
        //   label: "",
        //   _style: "width:6%",
        //   sorter: false,
        //   filter: false
        // }
      ],
      details: [],
      collapseDuration: 0
    };
  },

  computed: {},

  methods: {
    getBadge(status) {
      switch (status) {
        case "Active":
          return "success";
        case "Inactive":
          return "secondary";
        case "Pending":
          return "warning";
        case "Banned":
          return "danger";
        default:
          "primary";
      }
    },
    // get_icon (status){
    //   switch (status) {
    //     case 'Active': return '<b-icon-arrow-up class="text-success"></b-icon-arrow-up>'
    //     case 'Inactive': return 'secondary'
    //     case 'Pending': return 'warning'
    //     case 'Banned': return 'danger'
    //     default: 'primary'
    //   }
    // },
    toggleDetails(item) {
      this.$set(this.items[item.id], "_toggled", !item._toggled);
      this.collapseDuration = 300;
      this.$nextTick(() => {
        this.collapseDuration = 0;
      });
    },

    router_push(table_name, suffix) {
      // console.log("router_push", table_name, suffix);
      let path = "/" + this.index[this.route["app"]].name + "/" + table_name;
      if (suffix) {
        path += "/" + suffix;
      }
      this.$router.push({ path });
    }
  },

  beforeMount() {
    // console.log("App this.$route.params", this.$route.params);
    this.$store.commit("route", this.$route.params);
    this.$utils.get_index(this, "/api/v1/app");
  },

  mounted() {}
};
</script>
