import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

// CoreUi
// Installing whole package
import CoreuiVue from "@coreui/vue";
Vue.use(CoreuiVue);
// CoreUi

// vue-drawer-layout
import DrawerLayout from "vue-drawer-layout";
Vue.use(DrawerLayout);
// vue-drawer-layout

// axios
import axios from "axios";
Vue.prototype.$axios = axios;
// axios

// BootstrapVue
import { BootstrapVue, IconsPlugin } from "bootstrap-vue";

// Install BootstrapVue
Vue.use(BootstrapVue);
// Optionally install the BootstrapVue icon components plugin
Vue.use(IconsPlugin);
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
// BootstrapVue

// BootstrapVueIcons
import { BootstrapVueIcons } from "bootstrap-vue";
import "bootstrap-vue/dist/bootstrap-vue-icons.min.css";

Vue.use(BootstrapVueIcons);
// BootstrapVueIcons

// register all components globally
import * as ComponentMap from './components'
for (var componentName in ComponentMap) {
    Vue.component( componentName, ComponentMap[ componentName ] );
}
// register all components globally

// qs
import qs from "qs";
Vue.prototype.$qs = qs;
// qs

// config
// console.log("main ENV", ENV);
import ENV from "./config";
Vue.prototype.$ENV = ENV;
Vue.prototype.$axios.defaults.baseURL = ENV.HOST.HTTP;
// console.log("Vue.prototype.$axios", axios);
// config

import utils from "./utils";
Vue.prototype.$utils = utils;

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
