<template>
  <div id="app_wrap" class="card d-flex flex-row justify-content-center" :style="style">
    <app_jumbotron :index="index" :route="route" :app="app" />
    <app :index="index" :route="route" />
  </div>
</template>
<script>
export default {
  props: {},

  computed: {
    index() {
      return this.$store.getters.index;
    },
    route() {
      return this.$store.getters.route;
    },
    app() {
      // console.log("app", this.index, this.route, this.index[this.route["app"]]);
      return this.index[this.route["app"]];
    },
    style() {
      let url = "static/emergency/imgs/bg/circuit_03.svg";
      // if(this.app) {
      //   url = "static/emergency/imgs/icons/" + this.app.name + ".svg";
      // };
      let style =  {
        background: `linear-gradient(
          to bottom,
          rgba(8, 119, 34, 0.76) 0%,
          rgba(0, 0, 0, 0.99) 100%
        ),
        url(` + url + `) repeat 0 0`,
      }
      return style
    }
  }
};
</script>
