let set_table_height = function(height) {
  // 设置table与页面相适应的高度
  // console.log("$utils set_table_height", height);
  let table_height = document.documentElement.clientHeight - height;
  let table = document.getElementById("table");
  // let tbody_array = document.getElementsByTagName('tbody');
  // for (var i = tbody_array.length - 1; i >= 0; i--) {
  //   tbody_array[i].style.maxHeight = '200px';
  // }
  table.style.maxHeight = table_height + "px";
  // console.log('doc_height',doc_height,table)
};

let get_index = function(_this, api) {
  // 获取所有数据表列表数据
  _this.$axios
    .get(api)
    .then(response => {
      // console.log("$utils.get_index axios", response.data.data);
      _this.$store.commit("index", response.data.data);
      // console.log("$utils.get_index this.index", _this.index);
    })
    .catch(err => {
      // An error occurred
      console.log("err", err);
    });
};

let route_push = function(router, path, suffix) {
  console.log("route_push", path, suffix);
  if (suffix) {
    path += "/" + suffix;
  }
  router.push({ path }).catch(() => {
    // console.log("route_push err", err);
  });
}

