<template>
  <div class="card px-1" style="font-size: 0.8rem !important;">
    <breadcrumb class="my-1" />
    <div class="card">
      <!-- <div class="card-header">
      </div> -->
      <div class="card-body bg-light px-2 py-2" style="">
        <div id="formio" class=""></div>
      </div>
    </div>
  </div>
</template>
<script>
import { Formio, Templates } from "formiojs";

// console.log("Templates.current", typeof Templates.current, Templates.current, Templates.current.input);

// 重新覆盖定义每个component渲染form的html
// 参考：formio.js/docs/ast/source/templates/bootstrap/ 对应JSON文件内的raw

Templates.current = {
  // label: {
  //   // form: '<div class="col-sm-4 col-form-label col-form-label-sm">Label {{label.className}}  {{ t(component.label) }} </div>',
  //   form: `\n{% if (!label.hidden && label.labelPosition !== 'bottom') { %}\n  <label class="col-form-label col-form-label-sm col-6 mr-3 text-right {{label.className}}">\n    {{ t(component.label) }}\n    {% if (component.tooltip) { %} \n      <i ref="tooltip" class="{{iconClass('question-sign')}} text-muted" data-title="{{component.tooltip}}"></i>\n    {% } %}\n  </label>\n{% } %}\n \n{% if (!label.hidden && label.labelPosition === 'bottom') { %}\n  <label class="{{label.className}}">{{t(component.label)}}\n  {% if (component.tooltip) { %}\n    <i class="{{iconClass('question-sign')}} text-muted"></i>\n  {% } %}\n  </label>\n{% } %}\n{% if (component.description) { %}\n  <div class="form-text text-muted">{{t(component.description)}}</div>\n{% } %}\n`
  // },
  // input: {
  //   form: `{% input.attr['class'] += ' form-control-sm'; %}\n {% if (component.prefix || component.suffix) { %}\n<div class="input-group">\n{% } %}\n{% if (component.prefix) { %}\n<div class="input-group-prepend" ref="prefix">\n  <span class="input-group-text">\n    {{component.prefix}}\n  </span>\n</div>\n{% } %}\n<{{input.type}} \n  ref="{{input.ref ? input.ref : 'input'}}" \n  {% for (var attr in input.attr) { %}\n  {{attr}}="{{input.attr[attr]}}"\n  {% } %}\n>{{input.content}}</{{input.type}}>\n{% if (self.hasCounter) { %}\n<span class="text-muted pull-right" ref="counter"></span>\n{% } %}\n{% if (component.suffix) { %}\n<div class="input-group-append" ref="suffix">\n  <span class="input-group-text">\n    {{component.suffix}}\n  </span>\n</div>\n{% } %}\n{% if (component.prefix || component.suffix) { %}\n</div>\n{% } %}\n`
  // },
  // checkbox: {
  //   form: `\n <label class="col-form-label col-form-label-sm {{label.className}}">\n    {{ t(component.label) }}\n    {% if (component.tooltip) { %} \n      <i ref="tooltip" class="{{iconClass('question-sign')}} text-muted" data-title="{{component.tooltip}}"></i>\n    {% } %}\n  </label>\n \
  //   \n<div class="form-check" style="display: flex; align-items: center; justify-content: flex-start;">\n  <{{input.type}} \n style="margin-left: 0;"      ref="input" \n      {% for (var attr in input.attr) { %}\n      {{attr}}="{{input.attr[attr]}}"\n      {% } %}\n      {% if (checked) { %}checked=true{% } %}\n      >\n    {% if (component.tooltip) { %}\n      <i ref="tooltip" class="{{iconClass('question-sign')}} text-muted"></i>\n    {% } %}\n    {{input.content}}\n    </{{input.type}}>\n</div>\n`
  // },
  // button: {
  //   form: `\n <label class="col-form-label col-5 text-right {{label.className}}">\n </label>\n\
  //   \n<{{input.type}} \n  ref="button" \n  {% for (var attr in input.attr) { %}\n  {{attr}}="{{input.attr[attr]}}"\n  {% } %}\n>\n{% if (component.leftIcon) { %}<i class="{{component.leftIcon}}"></i>&nbsp;{% } %}\n{{input.content}}\n{% if (component.rightIcon) { %}&nbsp;<i class="{{component.rightIcon}}"></i>{% } %}\n</{{input.type}}>\n`
  // }
  // select: {
  //   form: `\n<select\n  ref="{{input.ref ? input.ref : 'selectContainer'}}"\n  {{ input.multiple ? 'multiple' : '' }}\n  {% for (var attr in input.attr) { %}\n  {{attr}}="{{input.attr[attr]}}"\n  {% } %}\n>{{options}}</select>\n{% input.attr['class'] += ' form-control-sm'; console.log('attr', typeof input.attr['class'], input.attr['class'],'instance', instance1, instance.choices); for (i in instance){console.log('i', i)} %}`
  // },
  // selectoption: {
  //   form: `"\n<option {{ selected ? 'selected="selected"' : '' }}\n  value="{{option.value}}"\n  {% for (var attr in attrs) { %}\n  {{attr}}="{{attrs[attr]}}"\n  {% } %}\n  >\n  {{t(option.label)}}\n</option>\n"`
  // }
};

// console.log('Templates', Templates, Templates.current)

export default {
  name: "Add",
  props: {},
  data() {
    return {
      api: {
        schema: "",
        lens: ""
      },
      schema: {
        app: {
          name: "",
          label: ""
        },
        components: []
      },
      submit: {
        type: "button",
        action: "submit",
        label: "Submit",
        theme: "primary",
        // leftIcon: `fa fa-check`,
        size: "sm"
      }
    };
  },

  methods: {
    get() {
      // 获取当前table的schema
      this.$axios.get(this.api.schema).then(async response => {
        console.log("get_schema axios", typeof response.data, response.data);
        this.schema = response.data;
        // Array.prototype.push.apply(this.schema.components, response.data.schema);
        // 获取当前pk的data 并为每个组件的defaultValue进行赋值
        if (this.$route.params.pk) {
          await this.$axios
            .get(this.api.lens + this.$route.params.pk)
            .then(response => {
              this.data = response.data.data.items[0];
              console.log("get() this.data", typeof this.data, this.data);
            });
          this.schema.components.map(cmp => {
            // 为每个组件的defaultValue进行赋值
            function json2string(item) {
              // 判断是否object 若是则转为string
              if (typeof item == "object") {
                // console.log("trans_json2string", typeof item, item);
                return JSON.stringify(item);
              }
              return item;
            }
            cmp["defaultValue"] = cmp.key
              ? json2string(this.data[cmp.key])
              : "";
            // console.log("map cmp", cmp);
          });
        }

        // 加上按钮
        this.schema.components.push(this.submit);
        // 渲染
        this.render_form();
      });
      // this.show_toast("加载状态", "数据渲染完成", "success", true);
    },

    gen_form_config() {
      // 构造form的config
      return {
        renderMode: "form",
        readOnly: false,
        noAlerts: false,
        language: "en",
        i18n: {
          en: {
            complete: "提交成功!",
            error: "请检查以下字段校验提示信息",
            Submit: "提交",
            submitError: "请检查并修复字段校验错误提示(已标红)",
            required: "{{field}} 是必填项"
          }
        },
        hooks: {
          // beforeSubmit: (submission, next) => {
          //   console.log("beforeSubmit", submission);
          //   // Alter the submission
          //   // submission.data.firstName = 'me@example.com';
          //   // Only call next when we are ready.
          //   next();
          // },
          customValidation: async (submission, next) => {
            // 自定义校验钩子
            // Call back-end APIs and handle response
            // console.log("customValidation", submission);
            let method = "post";
            let url = this.api.lens;
            let data = submission.data;
            if (this.$route.params.pk) {
              // 若有pk则转为pacth方法
              url += this.$route.params.pk;
              method = "patch";
            }
            console.log("customValidation", method, url);
            await this.$axios({ method: method, url: url, data: data }).then(
              response => {
                console.log(
                  "customValidation response",
                  method,
                  url,
                  submission,
                  response
                );

                if (Number(response.data.code) > 0) {
                  // 若新增数据成功

                  // 设置按钮状态 也可能代表整个表单状态
                  this.form.emit("submitDone", "提交成功!");

                  // 显示自定义alert
                  this.form.setAlert("success", this.form.t("complete"));
                  // console.log("form setAlert", this.form);
                  // this.set_style_alert();

                  // 进入下一阶段表单生命周期
                  next();
                } else {
                  // 若新增数据失败则给出错误信息
                  next(JSON.stringify(response.data.data));
                }
              }
            );
          }
        }
      };
    },

    async render_form() {
      let config = this.gen_form_config();
      Formio.createForm(
        document.getElementById("formio"),
        this.schema,
        config
      ).then(form => {
        // console.log("form", form);
        this.set_style_form(form);

        form.on("submit", submission => {
          // 自定义提交
          console.log("form submit", form, submission);
          let error = form.element.children[0];
          // console.log("error", error);
          error.children[0].className += " m-0 p-0";
          // this.post_or_patch(submission.data);
          // form.emit("submitError", "submission 233");
        });

        // form.on(
        //   "submitDone",
        //   message => {
        //     // 自定义提交成功
        //     console.log("form submitDone");
        //     let submitMessage = message || form.t("complete");
        //     console.log("form submitDone", message, submitMessage);
        //     this.loading = true;
        //     this.disabled = true;
        //     form.addClass(form.refs.button, 'btn-success submit-success');
        //     form.removeClass(form.refs.button, 'btn-danger submit-fail');
        //     form.addClass(form.refs.buttonMessageContainer, 'has-success');
        //     form.removeClass(form.refs.buttonMessageContainer, 'has-error');
        //     form.setContent(form.refs.buttonMessage, submitMessage);
        //   },
        //   true
        // );
        // form.on('submitError', (message) => {
        //   // 自定义提交错误
        //   let submitMessage = message || form.t(form.errorMessage('submitError'));
        //   console.log("form submitError", form, message, submitMessage);
        //   this.loading = false;
        //   this.disabled = false;
        //   this.hasError = true;
        //   form.removeClass(form.refs.button, 'btn-success submit-success');
        //   form.addClass(form.refs.button, 'btn-danger submit-fail');
        //   form.removeClass(form.refs.buttonMessageContainer, 'has-success');
        //   form.addClass(form.refs.buttonMessageContainer, 'has-error');
        //   form.setContent(form.refs.buttonMessage, submitMessage);
        // }, true);

        form.on("editComponent", data => {
          // Do something here.
          console.log("editComponent", data);
        });
        form.on("redraw", data => {
          // Do something here.
          console.log("redraw", data);
        });
        this.form = form;
        // form.emit("editComponent", form.components[1]);
      });
    },

    set_style_form(form) {
      /* 重调form的style */

      function add_class(element, klass) {
        // 为元素添加class
        // console.log("add_class(element, klass)", element, klass);
        element.className += klass;
      }

      form.components.map(cmp => {
        // console.log("component", cmp);

        // 是否required
        if (cmp.component.validate.required) {
          // console.log("add_class(element, klass) 0", cmp.element);
          let suffix =
            cmp.element.children[1].children[0].children[0].children[0];
          if (suffix) {
            add_class(suffix, " field-required");
          }
        }

        cmp.element.className += " mb-1 p-0";
        if (["checkbox"].includes(cmp.type)) {
          // div.form-group
          cmp.element.className += " form-row align-items-start px-2";

          //  div.form-check checkbox
          //    label.form-check-label
          cmp.element.children[0].children[0].className +=
            " d-flex align-items-center";

          //      input.form-check-input
          cmp.element.children[0].children[0].children[0].className +=
            " d-block mb-1";

          //      span
          cmp.element.children[0].children[0].children[1].className +=
            " d-block pl-1 pt-1";
          // console.log("checkbox", cmp);
        } else if (
          ["textfield", "textarea", "number", "time", "datetime"].includes(
            cmp.type
          )
        ) {
          // div.form-group
          //  label.col-form-lable
          cmp.element.children[0].className += " hide";

          //  div ref="element"
          //    div.input-group
          cmp.element.children[1].children[0].className += " input-group-sm";

          //      div.input-group-prepend
          //        span.input-group-text
          cmp.element.children[1].children[0].children[0].children[0].className +=
            " bg-blue-light text-sm";
          // console.log("textfield", cmp);
        } else if (["select"].includes(cmp.type)) {
          // div.form-group
          //  label.col-form-lable
          // cmp.element.children[0].className += " hide"
          cmp.element.children[0].className += " float-left py-0";

          //  div.choices form-group formio-choices
          cmp.element.children[1].className += " input-group input-group-sm";
          // cmp.element.children[1].innerHTML =
          // `
          //   <div ref="prefix" class="input-group-prepend">
          //     <span class="input-group-text bg-blue-light">
          //     事件单号
          //     </span>
          //   </div>
          // ` + cmp.element.children[1].innerHTML

          // console.log("select", cmp,);
        } else if (["button"].includes(cmp.type)) {
          // div.form-group
          cmp.element.className += " mt-3";

          //  button
          cmp.element.children[0].className += " mb-1";
          // console.log("button", cmp);
        }
      });

      // 每行label设置col-5
      // let labels = document.querySelectorAll("#formio > div.formio-component > div.formio-form > div.form-group > label.col-form-label");
      // for (let i = 0; i < labels.length; i++) {
      //   labels[i].className += " float-left";
      // }

      // 每个form_control
      // let form_control = document.getElementsByClassName("form-control");
      // for (let i = 0; i < form_control.length; i++) {
      //   form_control[i].className += " form-control-sm";
      // }
    },

    set_style_form_old(form) {
      // 调整form样式 已弃用

      // 每行以row显示
      form.components.map(cmp => {
        cmp.element.classList.add("row");
      });

      // 每行label设置col-5
      let labels = document.querySelectorAll(".col-form-label");
      for (let i = 0; i < labels.length; i++) {
        labels[i].className += " col-form-label-sm col-5 pr-3 text-right";
      }

      // 每行form设置col-5
      let element = document.querySelectorAll(
        "#formio > .formio-component > .formio-form > .formio-component > div[ref='element']"
      );
      // console.log("element", typeof element, element);
      for (let i = 0; i < element.length; i++) {
        element[i].className += " col-5";
      }

      // 每行select
      let formio_component_select = document.querySelectorAll(
        "#formio > .formio-component > .formio-form > .formio-component-select"
      );
      for (let i = 0; i < formio_component_select.length; i++) {
        let div = formio_component_select[i].querySelector(
          "div.formio-choices"
        );
        let input = formio_component_select[i].querySelector(
          "input.formio-select-autocomplete-input"
        );
        // console.log("div input", div, input)

        let element = document.createElement("div");
        element.setAttribute("ref", "element");
        element.setAttribute("class", "col-5 text-left");
        element.appendChild(div);
        element.appendChild(input);
        formio_component_select[i].appendChild(element);
      }

      // 每个form_control
      let form_control = document.getElementsByClassName("form-control");
      for (let i = 0; i < form_control.length; i++) {
        form_control[i].className += " form-control-sm";
      }

      // 单独设置button
      let button = document.querySelectorAll(".formio-component-button .btn");
      // console.log("button", typeof button, button);
      for (let i = 0; i < button.length; i++) {
        // let html = button[i].innerHTML;
        // button[i].innerHTML = `<label class="col-form-label col-form-label-sm col-6 mr-3 text-right"></label>` + html;
        let parent = button[i].parentNode;
        // let label = document.createElement("label");
        // label.className =
        //   "col-form-label col-form-label-sm col-5 pr-3 text-right";
        // parent.appendChild(label);

        button[i].className += " btn-sm";
        // parent.insertBefore(label, button[i]);

        let element = document.createElement("div");
        element.setAttribute("ref", "element");
        element.setAttribute("class", "col-5 text-left");
        element.appendChild(button[i]);
        parent.appendChild(element);
      }

      // 设置每行的marginBottom
      let form_group = document.querySelectorAll(
        "#formio > div.formio-component-form > div.formio-form > div.form-group"
      );
      for (var i = 0; i < form_group.length; i++) {
        form_group[i].style.marginBottom = "0.3rem";
      }
    }
  },

  beforeMount() {
    this.$store.commit("route", this.$route.params);
    this.$utils.get_index(this, "/api/v1/app");
    this.api.schema =
      "/api/v2/schema/" +
      this.$route.params.app +
      "/" +
      this.$route.params.table +
      "/";
    this.api.lens =
      "/api/v1/" +
      this.$route.params.app +
      "/" +
      this.$route.params.table +
      "/";

    // 加载数据:空表单
    this.get();
  },

  mounted() {}
};
</script>
